.App {
  text-align: center;
  position: relative;
  z-index: 0;
}

.App-logo {
  font-size: 3vw;
  padding-bottom: 5px;
  -webkit-animation: rotate 2s ease-in-out infinite;
          animation: rotate 2s ease-in-out infinite;
}

@-webkit-keyframes rotate {
	0%, 100% { -webkit-transform: rotate(20deg); transform: rotate(20deg); }
	25% { -webkit-transform: scale(1.1); transform: scale(1.1); }
	50% { -webkit-transform: rotate(-20deg); transform: rotate(-20deg); }
	75% { -webkit-transform: scale(.9); transform: scale(.9); }
}

@keyframes rotate {
	0%, 100% { -webkit-transform: rotate(20deg); transform: rotate(20deg); }
	25% { -webkit-transform: scale(1.1); transform: scale(1.1); }
	50% { -webkit-transform: rotate(-20deg); transform: rotate(-20deg); }
	75% { -webkit-transform: scale(.9); transform: scale(.9); }
}

.App-header {
  background:
  -webkit-radial-gradient(100% 50%, circle, transparent 20%, rgba(255,255,255,.3) 21%, rgba(255,255,255,.3) 34%, transparent 35%, transparent),
  -webkit-radial-gradient(0% 50%, circle, transparent 20%, rgba(255,255,255,.3) 21%, rgba(255,255,255,.3) 34%, transparent 35%, transparent) 0 -50px;
  background:
  -o-radial-gradient(100% 50%, circle, transparent 20%, rgba(255,255,255,.3) 21%, rgba(255,255,255,.3) 34%, transparent 35%, transparent),
  -o-radial-gradient(0% 50%, circle, transparent 20%, rgba(255,255,255,.3) 21%, rgba(255,255,255,.3) 34%, transparent 35%, transparent) 0 -50px;
  background:
  radial-gradient(circle at 100% 50%, transparent 20%, rgba(255,255,255,.3) 21%, rgba(255,255,255,.3) 34%, transparent 35%, transparent),
  radial-gradient(circle at 0% 50%, transparent 20%, rgba(255,255,255,.3) 21%, rgba(255,255,255,.3) 34%, transparent 35%, transparent) 0 -50px;
  background-color: dodgerblue;
  background-size:75px 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.App-title {
  font-size: 2vw;
  color: white;
}

.scrollToTop {
  position: fixed;
  z-index: 9999;
  bottom: 20px;
  right: 20px;
  cursor: pointer;
  opacity: 0.5;
}

@media only screen and (max-width: 1050px) {
  .App-header {
    font-size: 3vw;
  }
  .App-logo {
    font-size: 5vw;
  }

  .scrollToTop {
    height: 50px;
    bottom: 10px;
    right: 10px;
  }
}



